:root {
  /* Colors */
  --pink: #ce3e78;
  --white: #ffffff;
  --maroon: #681f3d;
  --black: #000000;
  --gray: #4b4949;

  /* Fonts */
  --chivo: "Chivo", sans-serif;
  /* --lora: "Lora", serif; */

  /* Font Sizes */
  --fs-xs: 0.75rem; /* 12px */
  --fs-sm: 0.875rem; /* 14px */
  --fs-md: 1rem; /* 16px */
  --fs-lg: 1.25rem; /* 20px */
  --fs-xl: 1.5rem; /* 24px */
  --fs-2xl: 2rem; /* 32px */
  --fs-2-5xl: 2.5rem;
  --fs-3xl: 3rem; /* 48px */
  --fs-4xl: 4rem;
  --fs-5xl: 6rem;
  --fs-6xl: 7rem;

  /* Font Weights */
  --fw-light: 300;
  --fw-regular: 400;
  --fw-medium: 500;
  --fw-semibold: 600;
  --fw-bold: 700;

  /* Background */
  --gradient: radial-gradient(
    60% 120% at 50% 50%,
    hsla(0, 0%, 100%, 0) 0%,
    rgba(247, 215, 237, 0.877) 100%
  );

  /* Cursors */
  --cursor-pointer: url("../assets/cursor-pointer-light.svg") 16 16, auto;
  --cursor-finger: url("../assets/cursor-finger-light.svg") 16 16, pointer;
}

[data-theme="dark"] {
  --pink: #d64e84;
  --white: #000000;
  --maroon: #f5b8d0;
  --black: #e9e9e9;
  --gray: #e9e6e6;

  --gradient: radial-gradient(60% 120% at 50% 50%, #1d1d1d 0%, #421b2b 100%);
  --cursor-pointer: url("../assets/cursor-pointer-dark.svg") 16 16, auto;
  --cursor-finger: url("../assets/cursor-finger-dark.svg") 16 16, pointer;
}
